<template>
  <div id="app">
    <transition :name="transitionName">
      <router-view class="child-view"/>
    </transition>
    <div class="wx">
      <router-link exact tag="p" to="/weixin/bindjupiter">账号绑定</router-link>
      <router-link tag="p" to="/weixin/firealarm">报警查询</router-link>
      <router-link tag="p" to="/weixin/alarmdetails">报警详情</router-link>
      <router-link tag="p" to="/weixin/datastatistics">数据报告</router-link>
      <router-link exact tag="p" :to="{path:'/weixin/bindjupiter',query:{pagetype:'unbundling'}}">解除绑定</router-link>
    </div>
  </div>
</template>
<!-- https://segmentfault.com/a/1190000012361988 -->

<script>
export default {
  name: "Login",
  data() {
    return {
      transitionName: "slide-left"
    };
  },
  methods: {},
  watch: {
    $route(to, from) {
      if (to.path == "/weixin/bindjupiter") {
        this.transitionName = "slide-right";
      } else if (to.path == "/weixin/datastatistics") {
        this.transitionName = "slide-left";
      }
    }
  }
};
</script>


<style>
#app {
  background: #fff;
  height: 100%;
  overflow: auto;
  position: relative;
}
#app .wx {
  position: fixed;
  bottom: 0;
  display: flex;
  width: 100%;
  box-shadow: 0 0 10px #ccc;
}
#app .wx p {
  flex: 1;
  color: #333;
  text-align: center;
  background: #fff;
}
#app .wx p:active {
  background: #ccc;
}
.child-view {
  position: absolute;
  width: 100%;
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}
#app .wx p.router-link-active{
  background: #ccc;
}
</style>
