import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import './assets/js/flexible.js';
import {GetRequest} from './assets/js/utils';
import './assets/less-css/initialize.css';
import './assets/less-css/base.css';
import './assets/icon/iconfont/iconfont.css';
Vue.config.productionTip = false;

// 微信sdk配置
const wx: any = (window as any).wx;
const {noncestr, sig, timestamp} = GetRequest();
wx.config({
  debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
  appId: 'wx2d21a81608716848', // 必填，公众号的唯一标识
  timestamp, // 必填，生成签名的时间戳
  nonceStr: noncestr, // 必填，生成签名的随机串
  signature: sig, // 必填，签名
  jsApiList: ['closeWindow'], // 必填，需要使用的JS接口列表
});
new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
